<template>
  <div class="careerGuidBox">
    <div class="careerGuid">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="已发布的就业指导" name="first">
          <div class="guidList">
            <el-table :data="guidList" style="width: 100%">
              <el-table-column label="序号" type="index" fixed width="60" align="center"></el-table-column>
              <el-table-column label="标题" prop="guidanceTitle" fixed align="center"></el-table-column>
              <el-table-column label="发布日期" prop="releaseDate" fixed align="center"></el-table-column>
              <el-table-column label="预览人数" prop="viewNum" fixed align="center"></el-table-column>
              <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"
                  >删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <el-tab-pane label="新增就业指导" name="second">
          <quillEditor :userInfo="userInfo" @submitSuccess="submitSuccess"></quillEditor>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import {
  getGuidanceInfo,
  interGuidanceInfo,
  addGuidanceInfo,
  deleteGuidanceInfo
} from "../../../../../utils/employment.url";
import quillEditor from "@/components/quillEdit/quillEditor.vue";
export default {
  components: { quillEditor },
  data() {
    return {
      userInfo: {},
      guidList: [],
      activeName: "first"
    };
  },
  methods: {
    //用户信息获取就业指导列表信息
    getGuidanceInfo() {
      getGuidanceInfo({ counselorNo: this.userInfo.userId }, "get")
        .then(res => {
          if (res.status == 200) {
            this.guidList = res.data;
            this.guidList.map((item, index) => {
              item.releaseDate = new Date(item.releaseDate)
                .toLocaleDateString()
                .replace(/\//g, "-");
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    handleClick(tab, event) {
      // console.log(tab, event);
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteGuidanceInfo(row, "delete").then(res => {
            if (res.status == 200) {
              this.getGuidanceInfo();
              this.$message({
                message: res.message,
                type: "success"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    submitSuccess(){
      this.getGuidanceInfo();
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getGuidanceInfo();
  }
};
</script>
<style lang="scss" scoped>
.careerGuidBox {
  width: 1000px;
  margin: 20px auto;
}
</style>