
<template>
  <div class="edit_container">
    <div class="title">
      <el-input v-model="guidanceTitle" placeholder="请输入就业指导标题"></el-input>
    </div>
    <quill-editor
      v-model="content"
      ref="myQuillEditor"
      :options="editorOption"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @change="onEditorChange($event)"
    ></quill-editor>
    <div class="careerImg">
      <!-- <el-upload
        class="avatar-uploader"
        :action="$http.defaults.baseURL +'/upload'"
        :show-file-list="false"
        :on-success="afterUpload"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb!</div>
      </el-upload> -->
    </div>
    <div class="submitGuidance">
      <el-button type="success" round @click="releaseGuid(userInfo.userId)">发布</el-button>
    </div>
  </div>
</template>
<script>
import { addGuidanceInfo } from "@/utils/employment.url";
import { quillEditor } from "vue-quill-editor"; //调用编辑器
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  components: {
    quillEditor
  },
  props: ["userInfo"],
  data() {
    return {
      guidanceTitle: "",
      content: ``,
      editorOption: {},
      fileList: []
    };
  },
  methods: {
    onEditorReady(editor) {
      // 准备编辑器
    },
    onEditorBlur() {
      console.log(this.content);
    }, // 失去焦点事件
    onEditorFocus() {}, // 获得焦点事件
    onEditorChange() {}, // 内容改变事件
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    releaseGuid(counselorNo) {
      // this.$refs.upload.submit();
      // console.log(this.fileList);
      var nowTime = new Date();
      let _releaseDate =
        nowTime.getFullYear() +
        "-" +
        (nowTime.getMonth() + 1) +
        "-" +
        nowTime.getDate() +
        " " +
        nowTime.getHours() +
        ":" +
        nowTime.getMinutes() +
        ":" +
        nowTime.getSeconds();
      let guidanceJson = {
        guidanceTitle: this.guidanceTitle,
        content: this.content,
        releaseDate: _releaseDate,
        viewNum: 0,
        counselorNo: counselorNo,
        fileList: this.fileList
      };
      addGuidanceInfo(guidanceJson, "post").then(res => {
        if (res.status == 200) {
          this.$emit("submitSuccess");
          this.$message({
            message: res.message,
            type: "success"
          });
        }
      });
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  // created: {}
};
</script>
<style lang="scss" scoped>
.edit_container {
  .title {
    margin: 10px auto;
  }
  .careerImg {
    width: 500px;
    margin: 10px;
  }
  .submitGuidance {
    margin: 20px auto;
    text-align: right;
  }
}
</style>
<style>
.ql-editor {
  height: 200px;
}
</style>